.sample {
    background-color: #cca;
    padding: 0 0 0 80px;
    align-items: end;
}
.title {
    width: 100%;
    font-size: 100px;
    text-align: center;
    margin: 0;
    margin-bottom: -50px;
    z-index: 10;
}
.line {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    position: relative;
    padding-right: 0;
    padding-bottom: 30px;
    border-bottom: 2px solid #fff;
    /* margin-top: -80px; */
}
